export default {
  name: 'CustomMessageInput',
  props: {
    message: {
      type: String,
      required: true,
      default: () => ''
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    focus() {
      this.$refs.input.focus();
    },

    /**
     * Notifies the parent component about a changed input
     *
     * @param {Event} event The Change Event
     */
    change(event) {
      this.$emit('change', event.target.value);
    },

    submit(event) {
      this.$emit('submit', event.target.value);
    }

  }
};